import React from "react";
import parse from "html-react-parser";

import freelancingPortraits from "../../../content/ContentDesigns";

const DesignPortraits = (props) => {
  return (
    <div className="text-center">
      {freelancingPortraits
        .filter((portrait) => !portrait.highlight)
        .map((portrait) => {
          return (
            <React.Fragment>
              <div className="portrait-design-row">
                <div className="overflow-hidden col-span-6 p-3">
                  <img src={portrait.portrait_image || null} />
                </div>
                <div className="overflow-hidden col-span-6 p-3">
                  <img src={portrait.original_image || null} />
                </div>
              </div>
            </React.Fragment>
          );
        })}
    </div>
  );
};

export default DesignPortraits;
